.main-nav {
    padding: 20px;
    font-size: 20px;
    background-color: whitesmoke;
    margin-bottom: 10px;
  }

  .move-right {
    float: right;
  }
  
  .nav-link-container {
    padding-right: 10px;
    margin-right: 10px;
    border-right: solid black 1px;
    display: inline-block;
  }

 
  
  .nav-link-container:last-of-type {
    padding-right: 0;
    border-right: none;
    margin-right: 0;
  }
  
  .nav-link {
    color: black;
    text-decoration: none;
    font-size: 14px;
  }
  .nav-link:not(.active):hover {
    color: purple;
  }
  
  .nav-link.active {
    color: darkgray;
  }

  